@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
@import url(http://fonts.cdnfonts.com/css/helvetica-neue-9);
:root {
    --spacing-one: calc(1rem * 0.5);
    --spacing-two: calc(1rem);
    --spacing-five: calc(2.5rem);

    --font-base: "Inter", "Helvetica Neue", Helvetica, Arial, sans-serif;
    --font-size-1: 5.4rem;
    --font-size-3: 2.4rem;
    --font-size-4: 1.8rem;
    --font-size-5: 1.4rem;
    --font-size-6: 1.3rem;

    --font-weight-bold: 700;
    --font-weight-semibold: 600;

    --line-height-heading: 1.2;
    --fs-800: 6.25rem;
    --fs-700: 3.5rem;
    --fs-600: 2rem;
    --fs-400: 1.125rem;

    --color-background-body: hsl(240, 7%, 6%);
    --color-background-base: hsl(240, 6%, 10%);

    --color-text-base: hsl(240, 7%, 94%);
    --color-text-alt: hsl(240, 8%, 88%);
    --color-text-button-secondary: hsl(240, 7%, 94%);

    --border-radius-medium: 0.4rem;
    --button-text-default: 1.3rem;
    --button-size-default: 3rem;
}

html,
body {
    height: 100%;
    line-height: 1.5rem;
}

main {
    margin: auto;
    max-width: 90%;
    padding: 50px 30px;
}

body {
    font-family: "Inter", "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-family: var(--font-base);
    font-size: 1.3rem;
    color: hsl(240, 7%, 94%);
    color: var(--color-text-base);
    background-color: hsl(240, 7%, 6%);
    background-color: var(--color-background-body);
    line-height: 1rem;
    min-height: 100vh;

    display: grid;
    grid-template-rows: -webkit-min-content 1fr;
    grid-template-rows: min-content 1fr;

    overflow-x: hidden;
    background: hsl(240, 7%, 6%);
    background-size: cover;
    margin: 0;
}

ul,
li {
    list-style: none;
}

header {
    align-items: center;
    justify-content: space-between;
    font-family: var(--font-display);
    text-align: center;
    white-space: nowrap;
    height: 100%;
    width: 100%;
}

header a {
    text-decoration: none;
    color: rgba(255, 255, 255, 0.75);
}

nav {
    background-color: hsl(240, 6%, 10%);
    background-color: var(--color-background-base);
    height: 3.3rem;
    box-shadow: 0px 1px 1px black;
    z-index: 2;
    display: flex;
    width: 100%;
    color: hsl(240, 7%, 94%);
    color: var(--color-text-base);
    padding: 0.5rem;
}

nav ul {
    display: flex;
    align-items: center;
    padding-left: 10px;
}

nav ul li {
    margin: 0 1.25rem;
    height: 100%;
    display: flex;
    align-items: center;

    line-height: 1.2;

    line-height: var(--line-height-heading);
    font-size: 1.4rem;
    font-size: var(--font-size-5);
    font-weight: 400;
}

nav ul li a:hover {
    color: hsl(264, 100%, 72%);
    cursor: pointer;
}

main {
    padding: 0;
}

section {
    margin-top: 2rem;
}
/* nav ul li[data-active='true'] {
    color: hsl(264, 100%, 72%);
    border-bottom: 0.125rem solid hsl(264, 100%, 72%);
} */

.active-nav {
    color: hsl(264, 100%, 72%);
    border-bottom: 0.125rem solid hsl(264, 100%, 72%);
}
/* ------------------------- */
/* Utility Classes */
/* ------------------------- */

.flex-row {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    grid-gap: var(--gap, 1rem);
    gap: var(--gap, 1rem);
    flex-wrap: wrap;
}

.px-1 {
    padding-right: calc(1rem * 0.5);
    padding-right: var(--spacing-one);
    padding-left: calc(1rem * 0.5);
    padding-left: var(--spacing-one);
}

.mx-2 {
    margin-right: calc(1rem);
    margin-right: var(--spacing-two);
    margin-left: calc(1rem);
    margin-left: var(--spacing-two);
}

.flex-row {
    display: flex;
    flex-wrap: wrap;
}

/* ------------------------- */
/* End of Utility Classes */
/* ------------------------- */

.nav-toggle {
    display: none;
}
