/* ------------------------- */
/* Works Card */
/* ------------------------- */
.title {
    font-family: var(--font-display);
    line-height: 1.2;
    line-height: var(--line-height-heading);
    font-size: 5.4rem;
    font-size: var(--font-size-1);
    font-weight: 700;
    font-weight: var(--font-weight-bold);
}
.card-carousel {
    max-width: 100%;
    display: grid;
    height: auto;
    grid-template-columns: repeat(auto-fit, minmax(120px, 1rem));
    grid-gap: 140px;
}
.card-carousel .card .card-img {
    transition: -webkit-transform 0.1s ease;
    transition: transform 0.1s ease;
    transition: transform 0.1s ease, -webkit-transform 0.1s ease;
}

.card-carousel .card .card-img:hover {
    -webkit-transform: translate(8px, -8px);
            transform: translate(8px, -8px);
    cursor: pointer;
}

.card .card-img img {
    height: 333px;
    width: 250px;
}

.card-carousel .card .card-img:hover img {
    box-shadow: 0 1px hsl(264, 100%, 72%), -1px 0 hsl(264, 100%, 72%),
        -2px 1px hsl(264, 100%, 72%), -3px 2px hsl(264, 100%, 72%),
        -3px 4px hsl(264, 100%, 72%), -4px 3px hsl(264, 100%, 72%),
        -4px 5px hsl(264, 100%, 72%), -5px 4px hsl(264, 100%, 72%),
        -5px 6px hsl(264, 100%, 72%), -6px 5px hsl(264, 100%, 72%),
        -6px 7px hsl(264, 100%, 72%), -7px 6px hsl(264, 100%, 72%),
        -7px 8px hsl(264, 100%, 72%), -8px 7px hsl(264, 100%, 72%);
}

.card-carousel .card .card-body .card-title {
    /* overflow: hidden; */
    white-space: nowrap;
    text-overflow: ellipsis;
    font-weight: 800;
    font-size: 0.938rem;
    margin-top: 0.625rem;
    margin-bottom: 0.313rem;
}

.card-body {
    font-size: 0.875rem;
    margin-top: 0;
    margin-bottom: 0.313;
    color: hsl(240, 7%, 94%);
    color: var(--color-text-base);
}

.card-body a {
    text-decoration: none;
    color: hsl(240, 7%, 94%);
    color: var(--color-text-base);
}
.card-body a:hover {
    color: hsl(264, 100%, 72%);
    cursor: pointer;
}

.card-subtext a {
    text-decoration: none;
    color: hsl(240, 7%, 70%);
    font-size: 1rem;
    font-weight: 300;
    line-height: 2;
}

.card-subtext a:hover {
    color: hsl(264, 100%, 72%);
    cursor: pointer;
}

.languages span {
    background: hsl(240, 2%, 20%);
    padding: 0.2rem 0.8rem;
    margin: 0.125rem;
    color: hsl(211, 14%, 71%);
    border-radius: 6.25rem;
    font-size: 0.75rem;
    font-weight: 800;
}

/* ------------------------- */
/* End of Works Card */
/* ------------------------- */

/* ------------------------- */
/* Start of About Section */
/* ------------------------- */

.about-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 2rem;
}

.ah-left {
    line-height: 1.5;
    width: 100%;
    display: flex;
    align-items: flex-start;
}

.about-header img {
    height: 6.25rem;
    width: 6.25rem;
    border-radius: 9000px !important;
}

.selfie {
    margin-right: 1rem;
}

.about-title {
    align-self: center;
}

.name {
    font-family: var(--font-display);
    line-height: 1.2;
    line-height: var(--line-height-heading);
    font-size: 2.4rem;
    font-size: var(--font-size-3);
    font-weight: 600;
    font-weight: var(--font-weight-semibold);
}
.headliner {
    font-size: 1.4rem;
    font-size: var(--font-size-5);
    color: hsl(240, 8%, 88%);
    color: var(--color-text-alt);
}

.ah-right {
    position: relative;
    display: flex;
}

.social-btn {
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    vertical-align: middle;
    overflow: hidden;
    text-decoration: none;
    white-space: nowrap;
    -webkit-user-select: none;
            user-select: none;
    font-weight: 600;
    font-weight: var(--font-weight-semibold);
    border-radius: 0.4rem;
    border-radius: var(--border-radius-medium);
    font-size: 1.3rem;
    font-size: var(--button-text-default);
    height: 3rem;
    height: var(--button-size-default);
    margin-right: 1rem;
    color: hsl(240, 7%, 94%);
    color: var(--color-text-button-secondary);
}

.btn-items {
    display: flex;
    align-items: center;
    flex-grow: 0;
    padding-top: 0px;
    padding-bottom: 0px;
    padding-left: calc(1rem - 0.2rem);
    padding-right: 1rem;
}

.linkedin {
    background-color: hsl(264, 100%, 64%);
}

.github {
    background-color: hsl(240, 2%, 20%);
}

.btn-items img {
    position: relative;
    display: flex;
    align-items: center;
    width: 30px;
    height: 30px;
    margin-right: 0.5rem;
    -webkit-filter: invert();
            filter: invert();
    right: 0.25rem;
}

.secondary-nav {
    display: flex;
    justify-content: space-between;
    flex-grow: 1;
    flex-shrink: 1;
    position: relative;
    height: 4rem;
    width: 100%;
    
}

.sn-ul {
    flex-grow: 1;
    display: flex;
    height: 100%;
    align-items: center;
    justify-content: flex-start;
    font-size: 1.8rem;
    font-size: var(--font-size-4);
    font-weight: 600;
    font-weight: var(--font-weight-semibold);
    line-height: 1.2;
    line-height: var(--line-height-heading);
    flex-wrap: wrap;
}
 .sn-ul li {
    list-style-position: inside;
    flex-grow: 0;
    align-items: center;
    justify-content: center;
    height: 100%;
 }

.nav-link {
    display: block;
    height: 100%;
    width: 100%;
    color: inherit;
    text-decoration: none;
    padding-left: 0rem;
    padding-right: 1rem;
    
}

.nav-text {
    color: hsl(264, 100%, 72%);
    cursor: pointer;
    display: flex;
    justify-content: center;
    flex-direction: column;
    flex-grow: 1;
}

.active-indicator {
    flex-grow: 0;
    margin: 0;
    padding: 0;
    height: 0.2rem;
    margin-bottom: -0.1rem;
    transition: -webkit-transform 0.2s ease 0s;
    transition: transform 0.2s ease 0s;
    transition: transform 0.2s ease 0s, -webkit-transform 0.2s ease 0s;
    -webkit-transform-origin: 0px 0px;
            transform-origin: 0px 0px;
    background-color: hsl(264, 100%, 72%);
}

.intro-container {
    padding-bottom: 1rem;
    padding-left: 4rem;
    padding-right: 4rem;
    max-width: 200rem;
    /* min-height: 60vh; */
    margin-left: auto;
    margin-right: auto;
    margin-top: 2rem;
    width: 90%;
    position: flex;
    flex-direction: column;
    display: flex;
    background-color: hsl(240,6%,10%);
    border-radius: 0.4rem;
    border-radius: var(--border-radius-medium);
}

.intro  {
    margin-top: 1.5rem;
}
.intro-text {
    line-height: 1.3;
}
/* ------------------------- */
/* End of About Section */
/* ------------------------- */
.sr-only:not(:focus):not(:active) {
    clip: rect(0 0 0 0);
    -webkit-clip-path: inset(50%);
            clip-path: inset(50%);
    height: 1px;
    overflow: hidden;
    position: absolute;
    white-space: nowrap;
    width: 1px;
}

